import React, { useState, useEffect } from "react";
import "semantic-ui-css/semantic.min.css";
import "date-fns";
import "./App.css";
import { storage } from "./firebase/firebase";
import imageCompression from "browser-image-compression";
import history from "./history";
import {
  Container,
  Button,
  Popup,
  Image,
  Input,
  Icon,
  TextArea,
  Form,
  Segment,
  Dropdown,
  Statistic,
  Divider,
  Dimmer,
  Loader,
  Pagination,
  Card,
  Label,
  Checkbox,
  Modal,
  Header,
} from "semantic-ui-react";
import { GoogleSpreadsheet } from "google-spreadsheet";

function Categorias({ user }) {
  const [data, setData] = useState("");
  const [catData, setCatData] = useState("");
  const [tipo, setTipo] = useState("");
  const [cat, setCat] = useState("");
  const [subCat, setSubCat] = useState("");
  const [saving, setSaving] = useState(false);
  const [open, setOpen] = useState(false);
  const [openSaved, setOpenSaved] = useState(false);

  //Fechas//
  let today = new Date();
  var dd = today.getDate();
  var mm = today.getMonth() + 1;
  var yyyy = today.getFullYear();
  var hora = today.getHours();
  var minutos = today.getMinutes();
  dd < 10 ? (dd = "0" + dd) : (dd = today.getDate());
  mm < 10 ? (mm = "0" + mm) : (mm = today.getMonth() + 1);
  if (hora < 10) hora = "0" + hora;
  if (minutos < 10) minutos = "0" + minutos;
  let date = yyyy + "-" + mm + "-" + dd;
  let fecha = date + " " + hora + ":" + minutos;
  //Fechas//

  var sheetNo = 2125477317;
  var sheetNo2 = 715064374;

  const sacardata = async () => {
    var datas = [];
    const creds = require("./sasso-app.json"); // the file saved above
    const doc = new GoogleSpreadsheet(
      "1N_nSzixyWg6OI7I7hTOQ-rfCpeln-pYV4FMtm17wIR0"
    );
    await doc.useServiceAccountAuth(creds);
    await doc.loadInfo(); // loads document properties and worksheets
    const sheet = doc.sheetsById[sheetNo];
    const rows = await sheet.getRows();
    let lastr = rows.length + 1;
    var j;
    await sheet.loadCells("A1:S" + lastr.toString());
    for (j = 1; j < lastr; j++) {
      const ar = sheet.getCell(j, 0);
      const br = sheet.getCell(j, 1);
      datas.push({
        Tipo: ar.value,
        Categoria: br.value,
      });
    }
    setData(datas);
  };

  useEffect(() => {
    if (!data) {
      sacardata();
    }
    if (tipo) {
      setCatData(
        data.filter((item) => {
          return item.Tipo === tipo;
        })
      );
      console.log(
        data.filter((item) => {
          return item.Tipo === tipo;
        })
      );
    }
  }, [data, tipo]);

  const handleCatClick = (e) => {
    setCat(e.target.name);
    console.log(e.target.name);
  };

  const handleTipoClick = (e) => {
    setTipo(e.target.name);
    console.log(e.target.name);
  };

  const handleGuardar = async () => {
    if ((tipo !== "") & (cat !== "") & (subCat !== "")) {
      setSaving(true);
      const creds = require("./sasso-app.json"); // the file saved above
      const doc = new GoogleSpreadsheet(
        "1N_nSzixyWg6OI7I7hTOQ-rfCpeln-pYV4FMtm17wIR0"
      );
      await doc.useServiceAccountAuth(creds);
      await doc.loadInfo(); // loads document properties and worksheets
      const sheet = doc.sheetsById[sheetNo2];
      const newData = await sheet.addRow({
        Tipo: tipo,
        Categoria: cat,
        SubCategoria: subCat,
        Autor: user,
        Fecha_Creacion: fecha,
      });
      setSubCat("");
      setSaving(false);
      setOpenSaved(true);
    } else {
      setOpen(true);
    }
  };

  const iniciar = () => {
    var priceEls = document.querySelectorAll(".item");
    for (var i = 0; i < priceEls.length; i++) {
      var ele = priceEls[i].innerText;
      console.log(ele);
      if (ele === "Usuario") {
        priceEls[i].click();
        break;
      }
    }
  };

  return (
    <Container>
      {user ? (
        <div>
          <Dimmer active={!data | saving} page>
            <Loader size="massive">{data ? "Saving" : "Loading"}</Loader>
          </Dimmer>
          <Modal
            basic
            onClose={() => setOpenSaved(false)}
            onOpen={() => setOpenSaved(true)}
            open={openSaved}
            size="small"
          >
            <Header icon>
              <Icon name="save" />
              Informacion Guardada
            </Header>
            <Modal.Content>
              <p>Su Informacion ha sido guardada.</p>
            </Modal.Content>
            <Modal.Actions>
              <Button
                color="green"
                inverted
                onClick={() => setOpenSaved(false)}
              >
                <Icon name="checkmark" /> Cerrar
              </Button>
            </Modal.Actions>
          </Modal>
          <Modal
            basic
            onClose={() => setOpen(false)}
            onOpen={() => setOpen(true)}
            open={open}
            size="small"
          >
            <Header icon>
              <Icon name="warning" />
              Informacion Incompleta
            </Header>
            <Modal.Content>
              <p>
                Debes escoger el tipo de observacio, la categoria y escribir la
                subcategoria a agregar.
              </p>
            </Modal.Content>
            <Modal.Actions>
              <Button color="green" inverted onClick={() => setOpen(false)}>
                <Icon name="checkmark" /> Cerrar
              </Button>
            </Modal.Actions>
          </Modal>
          <Segment>
            <Button.Group color="red" inverted fluid widths={4}>
              <Button
                name="Seguridad"
                onClick={handleTipoClick}
                active={tipo === "Seguridad"}
              >
                Seguridad
              </Button>
              <Button
                name="Calidad"
                onClick={handleTipoClick}
                active={tipo === "Calidad"}
              >
                Calidad
              </Button>
              <Button
                name="Ambiente"
                onClick={handleTipoClick}
                active={tipo === "Ambiente"}
              >
                Ambiente
              </Button>
              <Button
                name="Bioseguridad"
                onClick={handleTipoClick}
                active={tipo === "Bioseguridad"}
              >
                Bio Seguridad
              </Button>
            </Button.Group>
          </Segment>
          <Segment>
            {catData
              ? catData.map((item, i) => (
                  <div hidden={cat & (cat !== item.Categoria)}>
                    <Button
                      name={item.Categoria}
                      fluid
                      color="red"
                      inverted
                      active={cat === item.Categoria}
                      onClick={handleCatClick}
                    >
                      {item.Categoria}
                    </Button>
                    <br />
                  </div>
                ))
              : "Aqui van las Categorias"}
          </Segment>
          <Segment>
            <Form loading={false}>
              <Form.Field>
                <label>Sub Categoria</label>
                <TextArea
                  value={subCat}
                  onChange={(e) => {
                    setSubCat(e.target.value);
                    console.log(e.target.value);
                  }}
                  placeholder="Escriba la SubCategoria"
                ></TextArea>
              </Form.Field>
              <Button color="green" onClick={handleGuardar}>
                Submit
              </Button>
            </Form>
          </Segment>
        </div>
      ) : (
        <Button
          color="red"
          inverted
          fluid
          icon="key"
          content="Sign In"
          onClick={() => {
            iniciar();
          }}
        />
      )}
    </Container>
  );
}

export default Categorias;
